import { Injectable } from '@angular/core';
import * as moment from 'moment'


@Injectable()
export class DateConverterService {

    constructor() { }

    public jsdateObjectToMoment(jsDateObject){
        return moment(jsDateObject);
    }

    public mysqlDateTimeToMomentObj(mysqlDatetime: string){
        return moment(mysqlDatetime,'YYYY-MM-DD HH:mm:ss');
    }

    public mysqlCurrentDatetimeString(): string {
        // yyyy-mm-dd h:i:s
        return moment().format('YYYY-MM-DD HH:mm:ss');
    }

    private mysqlDatetimeToJSDateObject(mysqlDatetime: string): Date {
        return new Date(Date.parse(mysqlDatetime.replace('-', '/')));
    }

    /**
     * 
     * @param mysqlDatetime YYYY-MM-DD HH:mm:ss
     * @param mysqlTime HH:mm:ss
     */
    public mysqlTimeToJSDateObj(mysqlDatetime:string,mysqlTime: string) : Date{

        let aryTime : string[] = mysqlTime.split(':');
        let currentDate = moment(mysqlDatetime,'YYYY-MM-DD HH:mm:ss');
        
        currentDate.hour(parseInt(aryTime[0]));
        currentDate.minute(parseInt(aryTime[1]));
        currentDate.second(parseInt(aryTime[2]));

        

        return currentDate.toDate();
    }


}


