import { Injectable } from '@angular/core';

import { PaginateData } from './paginate-data';

@Injectable()
export class PaginatorService {
    public tableData:any[];
    public total:number;
    public totalPages: number;
    public perPage: number;
    public currentPage: number;
    public firstPage: number;
    public lastPage: number;
    public from: number;
    public to: number;
    public pageNumbers: number[];
    public hasMore:boolean;
    public paginateData: PaginateData;

    constructor() {
    }

    public init(paginateData: PaginateData) {
        this.paginateData = paginateData;
        this.total = this.paginateData.total;
        this.totalPages = this.paginateData.total;
        this.perPage = this.paginateData.per_page;
        this.currentPage = this.paginateData.current_page;
        this.firstPage = this.currentPage;
        this.lastPage = this.paginateData.last_page;
        this.from = this.paginateData.from;
        this.to = this.paginateData.to;
        this.tableData = this.paginateData.data;
    }

    public initPaginator(paginateData: PaginateData) {
        this.init(paginateData);

        this.totalPages = Math.ceil(this.total / this.perPage);

        if (this.totalPages <= 10) {
            // less than 10 total pages so show all
            this.firstPage = 1;
            this.lastPage = this.totalPages;
            this.hasMore = false;
        } else {
            // more than 10 total pages so calculate start and end pages
            this.hasMore = true;
            if (this.currentPage <= 6) {
                this.firstPage = 1;
                this.lastPage = 10;
            } else if (this.currentPage + 4 >= this.totalPages) {
                this.firstPage = this.totalPages - 9;
                this.lastPage = this.totalPages;
                this.hasMore = false;
            } else {
                this.firstPage = this.currentPage - 5;
                this.lastPage = this.currentPage + 4;
            }
        }

        // calculate start and end item indexes
        let startIndex = (this.currentPage - 1) * this.perPage;
        let endIndex = Math.min(startIndex + this.perPage - 1, this.totalPages - 1);

        // create an array of pages to ng-repeat in the pager control
        let i = this.firstPage;
        let count = this.lastPage;
        this.pageNumbers = [];
        while (i <= count) {
            this.pageNumbers.push(i);
            i++;
        }
        //let pages = _.range(startPage, endPage + 1);
    }
}


