import { Injectable } from '@angular/core';

@Injectable()
export class TableNameService {

public company : string;
public company_has_user : string;
public food : string;
public food_order : string;
public food_order_item : string;
public notification : string;
public stall : string;
public user : string;
constructor() { 
this.company = 'company'
this.company_has_user = 'company_has_user'
this.food = 'food'
this.food_order = 'food_order'
this.food_order_item = 'food_order_item'
this.notification = 'notification'
this.stall = 'stall'
this.user = 'user'
}
}


