import { Injectable, isDevMode } from '@angular/core';

@Injectable()
export class AppConfigService {

    private production_url: string;
    private development_url: string;
    private _url: string;
    public version: string;
    public copyRight: string;
    public appName: string;
    public apiKey : string;

    constructor() {
        this.production_url = "https://api.foodqr.com/";
        this.development_url = "https://andy.api.foodqr.com/";
        this.version = '1.00';
        this.copyRight = 'Andy';
        this.appName = 'FoodQR';
        this.apiKey  = 'CEelQ1pvH+cUbn69Qlo0Aiqy3R392zR7p9kJFL4Xuyo0';
    }

    public get url(): string {
        if (isDevMode()) {
            return this.development_url;
        } else {
            return this.production_url;
        }
    }





}


