import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { SimpleORMService } from '../../app/shared/simpleORM/simpleORM.service';
import { WebService } from '../../app/shared/restful-web-service/web.service';
import { AppConfigService } from '../../app/shared/AppConfig.service';
import { AuthService } from '../../app/shared/autheticate.service';
import { MyLogService } from '../../app/shared/mylog.service';
import { TableNameService } from '../../app/models/TableNameService.service';
import { MyHttpResponse } from '../../app/shared/restful-web-service/MyHttpResponse';
import { Md5 } from 'ts-md5/dist/md5';
import { UserModel } from '../../app/models/UserModel';

/**
 * Generated class for the LoginPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  public simpleORMService: SimpleORMService;
  public webService: WebService;
  public appConfigService: AppConfigService;
  public authServicec: AuthService;
  public myLogService: MyLogService;
  public tableNameService: TableNameService;

  public errorMessage: string;
  public username: string;
  public password: string;




  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    webService: WebService,
    appConfigService: AppConfigService,
    authServicec: AuthService,
    myLogService: MyLogService,
    tableNameService: TableNameService
  ) {
    this.webService = webService;
    this.appConfigService = appConfigService;
    this.authServicec = authServicec;
    this.myLogService = myLogService;
    this.tableNameService = tableNameService;

    this.username = '';
    this.password = '';
    this.errorMessage = '';
    
    

  }

  

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');

    this.myLogService.log('Login ngOnInit');
    if (this.authServicec.isAuthenticate == true) {
      this.myLogService.log('redirect to HomePage');
      //this.navCtrl.setRoot(HomePage);
    }
  }

  ionViewWillEnter(){
    this.username = localStorage.getItem('username');
    this.password = localStorage.getItem('password');
  }


  public onLoginClicked() {

    if(this.password == null || this.password == ''){
      this.errorMessage = 'The password can\'t be empty';
      return;
    }
    if(this.username == null || this.username == ''){
      this.errorMessage = 'The username can\'t be empty';
      return;
    }





    localStorage.setItem('username', this.username);
    //localStorage.setItem('password',this.password);

    let url = this.appConfigService.url + '/api/login';
    let parameters: any = {
      'username': this.username,
      'password': Md5.hashStr(this.password)
    };

    this.webService.httpJsonPost(url, parameters)
      .subscribe((responseData) => {
        this.myLogService.log('==== response data ======');
        this.myLogService.log(responseData);

        this.responseHandle(responseData);
      }, (error) => {
        this.errorMessage = 'Connection Failed'
      });
    
  }

  private responseHandle(jsonObject) {
    let myHttpResponse: MyHttpResponse = new MyHttpResponse(jsonObject);


    if (myHttpResponse.hasError == true) {
      this.errorMessage = myHttpResponse.message;
    } else {

      this.authServicec.isAuthenticate = true;
      this.errorMessage = '';
      let userData: any = myHttpResponse.data.user;
      let user: UserModel = userData as UserModel;
      this.authServicec.user = user;
      this.authServicec.token = myHttpResponse.data.token;
      console.log(this.authServicec);

      this.authServicec.updateSideBar = true;
      localStorage.setItem(this.tableNameService.user, JSON.stringify(user));
      //this.navCtrl.push();
      //this.navCtrl.setRoot(HomePage);
    }
  }

  /**
   * onRegisterClicked
   */
  public onRegisterClicked() {
    //this.navCtrl.push(RegisterPage);
  }

}



