import { Injectable } from '@angular/core';

@Injectable()
export class UtilitiesService {

    constructor() { }

    /**
     * mysqlDatetimeToJavascriptDateObject
     */
    public mysqlDatetimeToJavascriptDateObject(mySQLDatetime: string): Date {
        //let mySQLDate: string = '2015-04-29 10:29:08';

        return new Date(Date.parse(mySQLDatetime.replace('-', '/')));
    }


    /**
     * hexToDec
     */
    public hexToDec(hexString:string):number {
        let num:number = 0;
        for (let index = 0; index < hexString.length; index++) {
            let hexdigit = parseInt(hexString[index],16);
            num = (num << 4) | hexdigit;
        }

        return num;
    }
    

   

    /**
     * randomString
     */
    public randomString(length:number = 18) {

        var text = "";
        var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

        for (var i = 0; i < length; i++)
            text += possible.charAt(Math.floor(Math.random() * possible.length));

        return text;

    }



}


