import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ActionSheetController } from 'ionic-angular';
import { AuthService } from '../../app/shared/autheticate.service';
import { AppConfigService } from '../../app/shared/AppConfig.service';
import { WebService } from '../../app/shared/restful-web-service/web.service';
import { SimpleORMService } from '../../app/shared/simpleORM/simpleORM.service';


/**
 * Generated class for the ApiTestPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-api-test',
  templateUrl: 'api-test.html',
})
export class ApiTestPage {

  public apiUrl: string;
  public httpMethod: string;
  public bodyText: string;
  public requestValue: string;
  public totalSuccessRequest: number;
  public totalFailureRequest: number;
  public successResponse: string;
  public bodyTextJsonPretty: string;
  public totalTimeUsed: number;
  public timePerRequest: number;
  public requestPerTime: number;

  public simpleOrm: string;


  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public actionSheetCtrl: ActionSheetController,
    public appConfigService: AppConfigService,
    public webService: WebService,
    public simpleORMService: SimpleORMService
  ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ApiTestPage');
    this.apiUrl = localStorage.getItem('ApiTestPage.apiUrl');
    this.httpMethod = localStorage.getItem('ApiTestPage.httpMethod');
    this.bodyText = localStorage.getItem('ApiTestPage.bodyText');
    this.requestValue = localStorage.getItem('ApiTestPage.requestValue');

    console.log(parseFloat("55.5550").toFixed(2));


    this.totalSuccessRequest = 0;
    this.totalFailureRequest = 0;
    this.successResponse = '';
    this.bodyTextJsonPretty = '';
    this.totalTimeUsed = 0;
    this.requestPerTime = 0;
    this.timePerRequest = 0;

    if (this.apiUrl == null) {
      this.apiUrl = this.appConfigService.url;
      localStorage.setItem('ApiTestPage.apiUrl', this.apiUrl);
    }

    if (this.requestValue == null) {
      this.requestValue = '10';
      localStorage.setItem('ApiTestPage.requestValue', '10');
    }

    if (this.httpMethod == null) {
      this.httpMethod = 'POST';
      localStorage.setItem('ApiTestPage.httpMethod', this.httpMethod);
    }


    //========= Display the simple ORM json string =======
    this.simpleOrm = this.simpleORMService
      .table('company')
      .select('location', 'name', 'created_at', 'id', 'updated_at')
      .where('name', '=', 'Cyeroid Food')
      .where('location', '=', 'Toa Payoh')
      .getSqlQueryString();

      console.log('Simple ORM : '+this.simpleOrm);
      

  }

  beautifyJson() {
    console.log('Beautify JSON');

    this.bodyText = JSON.stringify(JSON.parse(this.bodyText), null, 4);
  }

  returnJson(text: string): string {
    return JSON.stringify(JSON.parse(text), null, 4);
  }

  showSubmitMethod() {
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Choose the Http Method',
      buttons: [
        {
          text: 'POST',
          handler: () => {
            this.httpMethod = 'POST';
          }
        }, {
          text: 'GET',
          handler: () => {
            this.httpMethod = 'GET';
          }
        }, {
          text: 'UPDATE',
          handler: () => {
            this.httpMethod = 'UPDATE';
          }
        }, {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }]
    });
    actionSheet.present();
  }

  submitRequest() {
    this.totalFailureRequest = 0;
    this.totalSuccessRequest = 0;

    localStorage.setItem('ApiTestPage.apiUrl', this.apiUrl);
    localStorage.setItem('ApiTestPage.requestValue', this.requestValue);
    localStorage.setItem('ApiTestPage.httpMethod', this.httpMethod);
    localStorage.setItem('ApiTestPage.bodyText', this.bodyText);

    let totalRequest: number = parseInt(this.requestValue);
    let startTime: number = (new Date()).getTime();

    let totalSuccessCount: number = 0;
    let totalFailureCount: number = 0;


    for (var index = 0; index < totalRequest; index++) {
      this.webService.httpJsonRequest(
        this.apiUrl,
        JSON.parse(this.bodyText),
        this.httpMethod,
        []
      ).subscribe(

        (data: any) => {
          totalSuccessCount += 1;
          if ((totalSuccessCount + totalFailureCount) == totalRequest) {
            this.totalSuccessRequest = totalSuccessCount;
            let endTime: number = (new Date()).getTime();
            try {
              this.successResponse = JSON.stringify(data, null, 4);
            } catch (error) {
              console.log('successResponse : '+this.successResponse);
              
            }
            this.totalTimeUsed = (endTime - startTime) / 1000;
            this.timePerRequest = parseFloat((this.totalTimeUsed / totalRequest).toFixed(6));
            this.requestPerTime = parseFloat((totalRequest / this.totalTimeUsed).toFixed(0));
          }
        },
        () => {
          totalFailureCount += 1;
          if ((totalSuccessCount + totalFailureCount) == totalRequest) {
            this.totalFailureRequest = totalFailureCount;
          }

        }
        );

    }

  }

}



