import { Injectable } from '@angular/core';
import { NotificationModel } from '../../models/NotificationModel';
import { DateConverterService } from '../DateConverter.service';
import { SimpleORMService } from '../simpleORM/simpleORM.service';
import { TableNameService } from '../../models/TableNameService.service';
import { GoogleFCMService } from '../googleFCM.service';
import { GooglePnResponse } from '../../objects/GooglePnResponse';
import { AppConfigService } from '../AppConfig.service';
import { UserModel } from '../../models/UserModel';

@Injectable()
export class PushNotificationService {

    constructor(
        public dateConverterService: DateConverterService,
        public tableNameService: TableNameService,
        public simpleORMService: SimpleORMService,
        public googleFCMService: GoogleFCMService,
        public appConfigService:AppConfigService
    ) { }

    public savePNMessage(message: string, sendTo: number, sendFrom: number) {
        let notificationModel: NotificationModel = new NotificationModel();
        notificationModel.message = message;
        notificationModel.created_at = this.dateConverterService.mysqlCurrentDatetimeString()
        notificationModel.send_from = sendFrom;
        notificationModel.send_to = sendTo;
        notificationModel.updated_at = notificationModel.created_at;

        
        this.simpleORMService.table(this.tableNameService.notification)
            .insert(notificationModel).subscribe();
    }

    public sendPNMessage(notificationMessage: string,sendTo : number) {
        this.simpleORMService.table(this.tableNameService.user)
            .where('id', '=', sendTo)
            .first().subscribe(
            (result: any) => {
                let storeUser: UserModel = result as UserModel;
                this.googleFCMService.sendSinglePNMessage(
                    storeUser.fcm_token,
                    this.appConfigService.appName,
                    notificationMessage,
                    {
                        "message" : notificationMessage
                    }
                ).subscribe(
                    (result : any) => {
                        console.log(result);
                        let googlePnResponse = result as GooglePnResponse;
                        if(googlePnResponse.success == 0){
                            // handle this error in future
                        }
                    },
                    (error) => {
                        
                        console.log(error);
                    }
                    );
            }
            );
    }

}


