import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiTestPage } from '../api-test/api-test';
import { MyEncryptionPage } from '../my-encryption/my-encryption';
import { RequestOptions, Http } from '@angular/http';

/**
 * Generated class for the HomePage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
})
export class HomePage {

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public http: Http
  ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HomePage');


     
       let headers: any = {
          'Content-Type':'text/plain',
          //'Content-Type': 'application/json',
       };
       
       let options = new RequestOptions({
          method: 'POST',
          headers: headers,
          body: 'String Text'
       });
       
       this.http.request('http://api.cyberoid.com', options).subscribe(
       (results: any) => {
          // Successful result
          console.log(results);
       },
       (error: any) => {
          // Failure result
          console.log('Error Http');
          console.log(error);
       }
       );
       
    

  }

  showAPIPage() {
    this.navCtrl.push(ApiTestPage);
  }

  showMyEncrytion() {
    this.navCtrl.push(MyEncryptionPage)
  }

}



