import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { ListPage } from '../pages/list/list';
import { LoginPage } from '../pages/login/login';
import { AuthService } from './shared/autheticate.service';
import { FirstPage } from '../pages/first/first';
import { ApiTestPage } from '../pages/api-test/api-test';
import { HomePage } from '../pages/home/home';
import { MyEncryptionPage } from '../pages/my-encryption/my-encryption';

@Component({
  templateUrl: 'app.html'
})


//sudo ionic cordova build ios --prod --aot --minifyjs
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = FirstPage;

  pages: Array<{title: string, component: any}>;

  constructor(
    public platform: Platform, 
    public statusBar: StatusBar, 
    public splashScreen: SplashScreen,
    public authService: AuthService
  ) {
    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [];
    this.initPages();
    

  }


  private initPages() {
    if(this.authService.updateSideBar == false && this.pages.length > 0){
      return;
    }
    
    this.authService.updateSideBar = false;

    if (this.authService.user != null) {
      if (this.authService.user.role == 'admin') {
        // set our app's pages
        this.pages = [
          
        ];
      } else if (this.authService.user.role == 'user') {
        this.pages = [
          //{ title: 'Order History', component: HistoryPage },
          
        ];
      }
    } else {
      this.pages = [
        //{ title: 'Logout', component: LogoutPage }
        { title: 'Home', component: HomePage },
        { title: 'Api Test', component: ApiTestPage },
        { title: 'My Encryption', component: MyEncryptionPage }
      ];
    }

  }

  ngDoCheck() {
    this.initPages();
    
    //this.isAuthenticated = this.authService.isAuthenticate;
  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }

  /**
   * getUserHandPhone
   */
  public getUserHandPhone() {
    if(this.authService.user == null){
      return '';
    }else{
      return this.authService.user.contact;
    }
  }

  public getUsername(){
    if(this.authService.user == null){
      return '';
    }else{
      return this.authService.user.username;
    }
  }

}



