import { Injectable } from '@angular/core';

@Injectable()
export class SortService {

    constructor() { }

    public numericSort(arrayValues: number[]): number[] {
        let results = arrayValues.sort(function (a, b) {
            return a - b;
        });
        return results;
    }

    public numericReverseSort(arrayValues: number[]): number[] {
        let results = arrayValues.sort(function (a, b) {
            return b - a;
        });
        return results;
    }

    public objectSort(objects: any[], targetVariable:any) {
        let results = objects.sort(function (a, b) {
            if (a[targetVariable] < b[targetVariable])
                return -1;
            if (a[targetVariable] > b[targetVariable])
                return 1;
        });

        return results;
    }

    public objectReverseSort(objects: any[], targetVariable:any) {
        let results = objects.sort(function (a, b) {
            if (a[targetVariable] < b[targetVariable])
                return 1;
            if (a[targetVariable] > b[targetVariable])
                return -1;
        });

        return results;
    }
}


