export class NotificationModel {

constructor() {
this.id = 0;
this.message= '';
this.send_to = 0;
this.send_from = 0;
this.created_at = '';
this.updated_at = '';
 }

public id: number;
public message: string;
public send_to: number;
public send_from: number;
public created_at: string;
public updated_at: string;
}



