import { Injectable } from '@angular/core';
import { TableNameService } from '../models/TableNameService.service';
import { UserModel } from '../models/UserModel';


@Injectable()
export class AuthService {

    private _user:UserModel;
    private _isAuthenticate:boolean;
	private _token:string;
	private _updateSideBar:boolean;

    constructor(public tableNameService : TableNameService) {
        
        this._user = null;
        this._isAuthenticate = false;
		this._token = '';
		this._updateSideBar = false;
    }

	/**
	 * init
	 */
	public init() {

		this._user = null;
		this._isAuthenticate = false;
		this._token = '';
		this._updateSideBar = false;
		
	}


	public get isAuthenticate(): boolean {
		let isAuthenticate = localStorage.getItem('isAuthenticate');
		if(isAuthenticate != null){
			return isAuthenticate == 'true'?true:false;
		}else{
			return this._isAuthenticate;
		}
		
	}

	public set isAuthenticate(value: boolean) {
		localStorage.setItem('isAuthenticate',value?'true':'false');
		this._isAuthenticate = value;
	}
	

	public get user(): UserModel {
		
		if(this._user != null){
			return this._user;
		}else{
			this._user = JSON.parse(localStorage.getItem(this.tableNameService.user)) as UserModel;
			return this._user;
		}
	}

	public set user(value: UserModel) {
		localStorage.setItem(this.tableNameService.user,JSON.stringify(value));
		this._user = value;
	}
    

	public get token(): string {
		if(this._token == ''){
			this._token = localStorage.getItem('token');
			return this._token;
		}else{
			return this._token;
		}
		
	}

	public set token(value: string) {
		localStorage.setItem('token',value);
		this._token = value;
	}


	public get updateSideBar(): boolean {
		return this._updateSideBar;
	}
	

	public set updateSideBar(value: boolean) {
		this._updateSideBar = value;
	}
	 
    

}


