import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Headers, RequestOptions, URLSearchParams } from '@angular/http';



import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class WebService {

    public http: Http;
    public url: string;
    public token: string;

    constructor(http: Http) {
        this.http = http;
        this.url = '';
        this.token = '';
    }

    public httpJsonRequest(url: string, jsonObject: any, method: string, headers: any = null): Observable<string> {
        if (headers == null) {
            headers = {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            };
        }

        let options  = new RequestOptions(
            {
                method: method,
                headers: headers,
                body: JSON.stringify(jsonObject)
            });
        


        return this.http.request(url, options)
            .map(this.extractData)
            .catch(this.handleError);

    }


    public httpPlainTextGet(url: string, parameters: URLSearchParams): Observable<string> {
        let headers = new Headers({ 'Content-Type': 'text/plain' });
        let options = new RequestOptions({ headers: headers });

        return this.http.get(url + '?' + parameters.toString(), options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    public httpPlainTextPost(url: string, parameters: URLSearchParams): Observable<string> {
        let headers = new Headers({ 'Content-Type': 'text/plain' });
        let options = new RequestOptions({ headers: headers });

        return this.http.post(url, parameters, options)
            .map(this.extractData)
            .catch(this.handleError);
    }

    public httpJsonPost(url: string, anyObject: any): Observable<string> {
        let headers = new Headers({
            'Content-Type': 'application/json',
            'Accept': 'application/json'
        });
        let options = new RequestOptions({ headers: headers });

        return this.http.post(url, JSON.stringify(anyObject), options)
            .map(this.extractData)
            .catch(this.handleError);
    }



    public laravelJsonPost(url: string, anyObject: any): Observable<string> {
        let headers = new Headers({
            'Content-Type': 'application/json',
            //'Authorization': 'Bearer ' + this.token,
            'Accept': 'application/json',
        });
        let options = new RequestOptions({ headers: headers });



        return this.http.post(url, JSON.stringify(anyObject), options)
            .map(this.extractData)
            .catch(this.handleError);
    }


    private extractData(res: Response) {
        try {
            let body = res.json();

            return body || res.text();
        } catch (error) {
            let body = res.text();
            return body || {};
        }

    }

    private handleError(error: Response | any) {
        console.log(error);
        // In a real world app, we might use a remote logging infrastructure
        let errMsg: string;
        if (error instanceof Response) {
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);
            errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        } else {
            errMsg = error.message ? error.message : error.toString();
        }
        //console.error(errMsg);
        return Observable.throw(errMsg);
    }



}






