import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule, IonicPageModule } from 'ionic-angular';

import { MyApp } from './app.component';
import { HttpModule } from '@angular/http';
import { ListPage } from '../pages/list/list';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { SimpleORMService } from './shared/simpleORM/simpleORM.service';
import { AuthService } from './shared/autheticate.service';
import { UtilitiesService } from './shared/utilities.service';
import { MyLogService } from './shared/mylog.service';
import { WebService } from './shared/restful-web-service/web.service';
import { MyEncryptionService } from './shared/myEncryption.service';
import { AppConfigService } from './shared/AppConfig.service';
import { PaginatorService } from './shared/paginate/paginator.service';
import { SortService } from './shared/sort.service';
import { TableNameService } from './models/TableNameService.service';
import { LoginPage } from '../pages/login/login';
import { FormsModule } from '@angular/forms';
import { DateConverterService } from './shared/DateConverter.service';
import { FCM } from '@ionic-native/fcm';
import { GoogleFCMService } from './shared/googleFCM.service';
import { PushNotificationService } from './shared/libraries/push-notification.service';
import { FirstPage } from '../pages/first/first';
import { ApiTestPage } from '../pages/api-test/api-test';
import { HomePage } from '../pages/home/home';
import { MyEncryptionPage } from '../pages/my-encryption/my-encryption';
import { MyEncryptionPageModule } from '../pages/my-encryption/my-encryption.module';
import { HomePageModule } from '../pages/home/home.module';
import { ApiTestPageModule } from '../pages/api-test/api-test.module';
import { FirstPageModule } from '../pages/first/first.module';




@NgModule({
  declarations: [
    MyApp,
    ListPage,
    LoginPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/api-test/api-test.module#ApiTestPageModule', name: 'ApiTestPage', segment: 'api-test', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/my-encryption/my-encryption.module#MyEncryptionPageModule', name: 'MyEncryptionPage', segment: 'my-encryption', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module#HomePageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/first/first.module#FirstPageModule', name: 'FirstPage', segment: 'first', priority: 'low', defaultHistory: [] }
  ]
}),
    MyEncryptionPageModule,
    HomePageModule,
    ApiTestPageModule,
    MyEncryptionPageModule,
    FirstPageModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    ListPage,
    LoginPage,
    HomePage,
    ApiTestPage,
    MyEncryptionPage,
    FirstPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    SimpleORMService,
    AuthService,
    UtilitiesService,
    MyLogService,
    WebService,
    MyEncryptionService,
    TableNameService,
    AppConfigService,
    PaginatorService,
    SortService,
    DateConverterService,
    FCM,
    GoogleFCMService,
    PushNotificationService
  ]
})
export class AppModule {}



